.footDiv{
    width:100%;
    height:0.98rem;
    position:fixed;
    bottom:0;
    left:0;
    background:#fff;
    .footUl{
        width:100%;
        height:100%;
        display:flex;
        justify-content: space-around;
        align-items:center;
        a{
            color:#6f6f6f;
            li{
                height:100%;
                display:flex;
                flex-direction:column;
                justify-content: space-around;
                align-items: center;
                .iconfont{
                    font-size:0.4rem;
                }
                .text-span{
                    font-size:0.2rem;
                }
            }
        }
        .nav-active,
        .router-link-exact-active {
          color: #dd4d41;
          border-color: #dd4d41;
        }
    }
}